import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { UserItem_Cmpt } from "..";
import {  search_user, get_bussines } from "../services/server_fetching";



const SearchInput = SearchInput_Cmpt();


const UserItem = UserItem_Cmpt();




const frmNm = "birthcertificate"




const UserDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [addview, setAddview] = useState(false);
	const [updview, setUpdview] = useState(false);
	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId());
			updStore("users_list",null)
			get_bussines(cb_result);
		}
	});


	const openUpdateView =  () => {
		setAddview(false)
		setUpdview(true)
	}


	



	



	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_account =  (v) => {
		set_loading(true)
		search_user(cb_result)
	}


	const handle_getall_account =  (v) => {
		
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	

	return (

	<div className="">
		<div className="accounts_header">
			<h2>Usuarios</h2>
			<div className="_dsplFlx ">
				<SearchInput
					placeholder={"Buscar cuenta"} 
					width={360}
					obs={set_obs}
					fetchData={handle_search_account}
					loading={loading}
					change={handleChange}
					callempty={handle_getall_account}
					keyFlds={"searchQry"}
				/>
				
				
			</div>
		</div>

		
	
		
	
		<div className="accounts_container">
			{getStore("users_list") && ObjectKeys(getStore("users_list"))?.map(cid=>{
				return (
					<UserItem 
						data={getStore("users_list")[cid]} 
						elmId={cid} 
						key={cid}
						openUpdateView={openUpdateView}
					/>
				)
			})}
		</div>
	
		</div>
	);
};

export default UserDashboard;

