import { fetchEncryptStream, updStore, getStore, gen16CodeId, convertArray2ObjGroupby, convertArray2Obj, qryStore } from "../../../stores/Utils";

export const updateUserDetails = async (cb) => {
    let bdyq2 = {
        query: "updateUserDetails",
        params: {
            limit: 250,
            userId: ""
        },
       
    };
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    //console.log(td);
    if (td && !td.error) {
        
        cb && cb();
    }
};


export const search_user = async (cb) => {
    let bdyq2 = {
        query: "getUsersByParams",
        params: {
            limit: 250,
            ":search": getStore("searchQry")
        },
        queryString: "!* contain :search",
    };
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    if (td && !td.error) {
        updStore("users_list", td);
        cb && cb();
    }
};



export const get_bussines = async (cb) => {
    
    let bdyq2 = {...getStore(qryStore())?.all_busnss};
    const res = fetchEncryptStream(bdyq2);
    const td = await res;
    // console.log(td)
    if (td && !td.error) {
        let newA = []
        td.map(d=>[
            newA.push( {label: d.name, id: d.businessId})
        ])
        updStore("bussines_list", newA);
        cb && cb();
    }
};